* {
	margin: 0;
	border: 0;
	padding: 0;
	font-family: Arial, Helvetica, sans-serif;
	box-sizing: border-box;
	outline: none;
}

:root {
	--red: #701c1c;
	--green: #4cb944;
	--lightgreen: #69ce5f;
	--peach: #ffe6a7;
	--camel: #bb9457;
	--dark: #555555;
	--light: #dddddd;
	--blue: #0073bb;
	--border-radius: 6px;
	--box-shadow: 2px 2px 10px -4px;
	--transition: linear 0.3s;
}

.Application {
	height: 100vh;
	width: 100vw;
}

.Authentication {
	height: 100vh;
	width: 100vw;
	overflow: hidden;
}

.AuthForm {
	height: 80%;
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
}

.AuthForm h2 {
	color: var(--green);
}

.AuthForm a {
	color: var(--blue);
	text-decoration: none;
}

.AuthForm a:hover {
	text-decoration: underline;
}

.AuthForm form {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.AuthForm span {
	color: var(--dark);
	font-size: 14px;
}

.AuthForm-input {
	width: 60%;
	border: 1px solid var(--dark);
	border-radius: var(--border-radius);
	padding: 10px;
	font-size: 18px;
	margin: 10px;
}

.AuthForm-submit {
	width: 60%;
	border-radius: var(--border-radius);
	padding: 10px;
	font-size: 18px;
	margin: 10px;
	color: white;
	background-color: var(--green);
	transition: 0.5s linear;
}

.AuthForm-submit:hover {
	background-color: var(--lightgreen);
	transition: 0.5s linear;
}

.AuthForm div {
	width: 60%;
	border-bottom: 1px solid var(--dark);
	text-align: center;
}

.Banner {
	background-color: var(--green);
	color: white;
	padding: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.Credits {
	width: 100%;
	background: linear-gradient(120deg, var(--green), var(--lightgreen));
	box-sizing: border-box;
	padding: 20px;
	display: flex;
	flex-direction: column;
}

.Credits svg {
	height: 50px;
	width: auto;
	color: var(--light);
	padding: 20px;
	box-sizing: content-box;
}

.Credits svg:hover {
	color: white;
}

.Credits p {
	color: white;
	height: 20px;
}

.Landing {
	height: 100vh;
	width: 100%;
}

.Landing-Benefits {
	height: 50%;
	width: 100%;
}

.Landing-Categories {
	height: 50%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	color: var(--green);
	background-color: #eeeeee;
}

.Landing-Categories div {
	color: black;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.Landing-Categories-Card {
	height: 100px;
	width: 200px;
	margin: 10px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background-color: white;
	color: var(--dark);
	border-radius: var(--border-radius);
	border: 1px solid var(--light);
	text-decoration: none;
}

.Landing-Categories-Card:hover {
	border: 1px solid var(--lightgreen);
	color: var(--lightgreen);
	box-shadow: var(--box-shadow);
}

.Landing-Categories-Card svg {
	height: 44px;
	width: auto;
}

.Landing-Search {
	height: 70%;
	width: 100%;
	background-color: var(--light);
	background-image: url(../assets/over.jpg);
	background-position: 50%;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.Landing-Tint {
	position: absolute;
	height: 70%;
	width: 100%;
	background: rgba(0, 0, 0, 0.5);
}

.NavBar {
	position: fixed;
	height: 100px;
	width: 100%;
	padding: 0 10%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: white;
	grid-area: nav;
	z-index: 1;
}

.NavBar ul {
	height: 100%;
	min-width: 150px;
	list-style: none;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.NavItem {
	color: white;
	padding: 10px 0;
	font-weight: 700;
	text-decoration: none;
}

.NavItem:hover {
	border-bottom: 2px solid white;
}

.User {
	min-width: auto !important;
	position: relative;
	height: 63px;
	display: flex;
	align-items: center;
	background-color: var(--dark);
	border-radius: var(--border-radius);
	color: white;
}

.User img {
	height: 100%;
	border-radius: 6px 0 0 6px;
}

.UserDropdown {
	position: absolute;
	bottom: -155px;
	right: 0;
	background-color: var(--dark);
	height: 150px;
	width: 140px;
	border-radius: var(--border-radius);
}

.UserDropdown button {
	width: inherit;
	background: transparent;
	color: white;
	padding: 10px;
	border-radius: var(--border-radius);
	font-size: 14px;
	font-weight: 700;
}

.UserDropdown button:hover {
	background-color: #888888;
}
